/***********************************
    Floating Styles
***********************************/
.float-left {
    float: left !important;
    &--lg {
        float: none !important;
        @include media(lg) {
            float: left !important;
        }
    }
    &--med {
        float: none !important;
        @include media(med) {
            float: left !important;
        }
    }
    &--sm {
        float: none !important;
        @include media(sm) {
            float: left !important;
        }
    }
}
.float-right {
    float: right !important;
    &--lg {
        float: none !important;
        @include media(lg) {
            float: right !important;
        }
    }
    &--med {
        float: none !important;
        @include media(med) {
            float: right !important;
        }
    }
    &--sm {
        float: none !important;
        @include media(sm) {
            float: right !important;
        }
    }
}
.clearfix:after {
    display: block !important;
    visibility: hidden !important;
    clear: both !important;

    height: 0 !important;

    content: ' ' !important;
    font-size: 0 !important;
}