/***********************************
    External Spacing
***********************************/
.h-isolate {
    margin: $spacingBase !important;
    &--xsm {
        margin: $spacingXSm !important;
    }
    &--sm {
        margin: $spacingSm !important;
    }
    &--lg {
        margin: $spacingLg !important;
    }
    &--xlg {
        margin: $spacingXLg !important;
    }
}
.h-isolateEnds {
    margin: $spacingBase 0 !important;
    &--xsm {
        margin: $spacingXSm 0 !important;
    }
    &--sm {
        margin: $spacingSm 0 !important;
    }
    &--lg {
        margin: $spacingLg 0 !important;
    }
    &--xlg {
        margin: $spacingXLg 0 !important;
    }
}
.h-isolateSides {
    margin: 0 $spacingBase !important;
    &--xsm {
        margin: 0 $spacingXSm !important;
    }
    &--sm {
        margin: 0 $spacingSm !important;
    }
    &--lg {
        margin: 0 $spacingLg !important;
    }
    &--xlg {
        margin: 0 $spacingXLg !important;
    }
}
.h-isolateBottom {
    margin: 0 0 $spacingBase !important;
    &--xsm {
        margin: 0 0 $spacingXSm !important;
    }
    &--sm {
        margin: 0 0 $spacingSm !important;
    }
    &--lg {
        margin: 0 0 $spacingLg !important;
    }
    &--xlg {
        margin: 0 0 $spacingXLg !important;
    }
}
.h-isolateTop {
    margin: $spacingBase 0 0 !important;
    &--xsm {
        margin: $spacingXSm 0 0 !important;
    }
    &--sm {
        margin: $spacingSm 0 0 !important;
    }
    &--lg {
        margin: $spacingLg 0 0 !important;
    }
    &--xlg {
        margin: $spacingXLg 0 0 !important;
    }
}
.h-isolateLeft {
    margin: 0 0 0 $spacingBase !important;
    &--xsm {
        margin: 0 0 0 $spacingXSm !important;
    }
    &--sm {
        margin: 0 0 0 $spacingSm !important;
    }
    &--lg {
        margin: 0 0 0 $spacingLg !important;
    }
    &--xlg {
        margin: 0 0 0 $spacingXLg !important;
    }
}
.h-isolateRight {
    margin: 0 $spacingBase 0 0 !important;
    &--xsm {
        margin: 0 $spacingXSm 0 0 !important;
    }
    &--sm {
        margin: 0 $spacingSm 0 0 !important;
    }
    &--lg {
        margin: 0 $spacingLg 0 0 !important;
    }
    &--xlg {
        margin: 0 $spacingXLg 0 0 !important;
    }
}