body {
   @extend %flex;

   @include fontSize($fontSizeBase);
   position: relative;
   left: 0;

   overflow-x: hidden;
   overflow-y: scroll;

   flex-direction: column;

   min-width: 100%;
   height: 100%;
   margin: 0;

   font-family: $fontBase;

   color: $colorGreyDarker;
   background: $colorWhite;
}
