/*********************************
		Colors
*********************************/
//Monochrome Colors
$colorWhite: #fff;
$colorGreyLightest: darken($colorWhite, 12.5%);
$colorGreyLighter: darken($colorWhite, 25%);
$colorGreyLight: darken($colorWhite, 37.5%);
$colorGrey: darken($colorWhite, 50%);
$colorGreyDark: darken($colorWhite, 62.5%);
$colorGreyDarker: darken($colorWhite, 75%);
$colorGreyDarkest: darken($colorWhite, 87.5%);
$colorBlack: darken($colorWhite, 100%);

//Alert Colors
$colorSuccess: #00b200;
$colorWarning: #ffd700;
$colorError: #990000;

//Brand Colors
$colorBrand5: #5D5E5E;
$colorBrand4: #F8981D;
$colorBrand3: #4DAF4F;
$colorBrand2: #009789;
$colorBrand1: #3292D0;